@import 'styles/sass/main';

.LoginInput {
  width: 100%;
  margin-bottom: 16px;

  @media (max-width: $md), (max-device-width: $md) {
    margin-bottom: 32px;
  }

  &__header {
    @include flex(space-between);
    font: 400 11px/12px $helvetica-regular;
    color: $black-gray;
    letter-spacing: -0.18px;

    @media (max-width: $md), (max-device-width: $md) {
      margin-top: 8px;
      font-size: 12px;
      line-height: 14px;
    }
    @media (max-width: $sm), (max-device-width: $sm) {
      font-size: 14px;
      line-height: 16px;
    }
  }

  &__btn {
    @include btn-reset;
    font: 400 11px/12px $helvetica-regular;
    color: $blue-dark;
    letter-spacing: -0.18px;
    display: none;

    @media (max-width: $md), (max-device-width: $md) {
      font-size: 12px;
      line-height: 14px;
    }
    @media (max-width: $sm), (max-device-width: $sm) {
      font-size: 14px;
      line-height: 16px;
    }

    &_active {
      display: block;
    }
  }

  &__input-block {
    position: relative;
    width: 100%;
    height: 30px;
    margin-top: 8px;
  }

  &__input {
    @include inp-reset;
    width: 100%;
    height: 100%;
    padding: 8.5px 11.5px;
    font: 400 11px/13px $helvetica-regular;
    color: $blue-dark;
    letter-spacing: -0.19px;
    border: 0.5px solid $gray-blue;
    box-sizing: border-box;
    border-radius: 3px;

    @media (max-width: $md), (max-device-width: $md) {
      font-size: 12px;
      line-height: 14px;
    }
    @media (max-width: $sm), (max-device-width: $sm) {
      font-size: 14px;
      line-height: 16px;
    }

    &_error {
      border-color: $red;
    }

    &::placeholder {
      color: $gray;
    }

    &:focus {
      border-color: $blue;
    }
  }

  &__watch-password {
    display: none;
    @include btn-reset;
    position: absolute;
    right: 15px;
    top: calc(50% - 13px);
    width: 25px;
    height: 26px;
    background: $white;
    color: $blue;

    &_active {
      @include flex(center, center);
    }
  }

  &__error {
    display: none;
    position: absolute;
    bottom: -16px;
    left: 0;
    width: 100%;
    height: 11px;
    padding: 0 11.5px;
    font: 400 10px/11px $helvetica-regular;
    color: $red;
    letter-spacing: -0.18px;

    @media (max-width: $md), (max-device-width: $md) {
      bottom: -12px;
      font-size: 12px;
      line-height: 14px;
    }
    @media (max-width: $sm), (max-device-width: $sm) {
      bottom: -12px;
      font-size: 14px;
      line-height: 16px;
    }

    &_active {
      display: block;
    }
  }
}
